/* Table styling */
table {
  border: 1px solid #ddd;
  width: 100%;
  margin-top: 2rem;
  border-collapse: collapse;
}

th,
td {
  padding: 0.5rem;
  border-bottom: 1px solid #ddd;
  border-right: 1px solid #ddd;
}

th:last-child,
td:last-child {
  border-right: none;
}

th {
  background-color: #f2f2f2;
}

tr:hover {
  background-color: #f5f5f5;
}
